import {customElement, findElementsInSlot} from '@here/hds-base';
import {html, LitElement, PropertyValues} from 'lit';
import {property, query} from 'lit/decorators.js';
import {TabPanel} from './hds-tab-panel.js';

/**
 * @slot default Container for the `hds-tab-panel` elements
 */
@customElement('hds-tab-panel-container')
export class TabPanelContainer extends LitElement {
  @property({type: String, reflect: true, attribute: 'tab-bar-id'}) tabBarId = '';
  @property({type: String, reflect: true, attribute: 'selected-panel'}) selectedPanel = '';

  @query('slot') protected panelSlot!: HTMLSlotElement;

  private panelElements: TabPanel[] = [];

  protected updated(_changedProperties: PropertyValues) {
    if (_changedProperties.has('selectedPanel')) {
      this._setSelectedPanel(this.selectedPanel);
    }
  }

  protected firstUpdated() {
    this._onSlotChange();
  }

  protected render() {
    return html` <slot @slotchange="${this._onSlotChange}"></slot>`;
  }

  private _onSlotChange() {
    this.panelElements = findElementsInSlot<TabPanel>(this.panelSlot, TabPanel);
    this._setSelectedPanel(this.selectedPanel);
  }

  private _setSelectedPanel(panelId: string) {
    this.panelElements.forEach(panel => {
      // eslint-disable-next-line no-param-reassign
      panel.show = panel.id === panelId;
    });
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'hds-tab-panel-container': TabPanelContainer;
  }
}
